import {create} from 'zustand'
import {immer} from 'zustand/middleware/immer'

const useStore = create(
  immer((set) => ({
    didInit: false,
    images: null,
    layout: 'sphere',
    layouts: null,
    nodePositions: null,
    highlightNodes: null,
    isSidebarOpen: false,
    targetImage: null,
    resetCam: false,
    cameraCurrentZ: 0,
  }))
)

export default useStore
