import React from "react";
import c from "clsx";
import useStore from "../store.js";
import { setSidebarOpen, setTargetImage } from "../actions.js";

const truncateDescription = (description, wordLimit = 7) => {
  if (!description) {
    return "";
  }
  const words = description.split(" ");
  if (words.length <= wordLimit) {
    return description;
  }
  return words.slice(0, wordLimit).join(" ") + " ...";
};

const Sidebar = () => {
  console.log("[Sidebar.jsx] Rendering Sidebar component");
  const {images, targetImage, isSidebarOpen} =
    useStore(state => ({
      images: state.images,
      targetImage: state.targetImage,
      isSidebarOpen: state.isSidebarOpen,
    }))
  console.log("[Sidebar.jsx] Values from useStore:", { imagesLength: images?.length, targetImage, isSidebarOpen });

  const selectedImage = images ? images.find(img => img.id === targetImage) : null;
  console.log("[Sidebar.jsx] selectedImage:", selectedImage ? selectedImage.id : null);

  // Sort images by ID for consistent order, only if images is available
  const sortedImages = images ? [...images].sort((a, b) => a.id.localeCompare(b.id)) : [];
  console.log("[Sidebar.jsx] sortedImages length:", sortedImages.length);

  const toggleSidebar = () => {
    setSidebarOpen(!isSidebarOpen);
  };

  console.log("[Sidebar.jsx] Returning JSX");
  return (
    <div
      className={`fixed top-0 right-0 h-full bg-gray-800 bg-opacity-90 text-white p-4 transition-transform duration-300 ease-in-out transform ${isSidebarOpen ? 'translate-x-0' : 'translate-x-full'} w-80 md:w-96 z-30 shadow-lg`}>
      <button
        onClick={toggleSidebar}
        className="absolute top-4 right-4 text-white hover:text-gray-300 transition-colors text-2xl z-40 w-8 h-8 flex items-center justify-center">
        ×
      </button>

      {selectedImage && (
        <div className="mb-6 pt-8">
          <h2 className="text-2xl font-bold mb-3">Selected Image</h2>
          <div className="bg-gray-700 p-3 rounded-lg shadow-md">
            <img
              src={`${import.meta.env.BASE_URL}assets/images/${selectedImage.id}`}
              alt={selectedImage.description}
              className="w-full h-auto object-cover rounded-md mb-2 shadow-sm"
            />
            <p className="text-sm text-gray-300 leading-relaxed">
              {truncateDescription(selectedImage.description, 3)}
            </p>
          </div>
        </div>
      )}

      <ul>
        {sortedImages.map((image) => (
          <li
            key={image.id}
            onClick={() => setTargetImage(image.id)}
            className={`cursor-pointer p-2 hover:bg-gray-700 rounded-md transition-colors duration-150 flex items-center space-x-3 ${targetImage === image.id ? 'bg-blue-500 bg-opacity-40' : ''}`}>
            <img
              src={`${import.meta.env.BASE_URL}assets/images/${image.id}`}
              alt={truncateDescription(image.description, 3)}
              className="w-16 h-16 object-cover rounded-sm shadow-sm"
            />
            <div>
              <p>{truncateDescription(image.description, 3)}</p>
            </div>
          </li>
        ))}
        {(!images || images.length === 0) && <li>No images available.</li>}
      </ul>
    </div>
  );
};

export default Sidebar;
