import React, { useState, useRef, useEffect } from "react";
import { Billboard } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import * as THREE from "three";
import { setTargetImage } from "../actions.js";

const aspectRatio = 1;
const thumbHeight = 16;
const thumbWidth = thumbHeight * aspectRatio;

export const PhotoNode = ({
  id,
  position,
  targetImage,
}) => {
  const textureUrl = `${import.meta.env.BASE_URL}assets/images/${id}`;
  console.log(`[PhotoNode.jsx] Loading texture for ID ${id} from URL: ${textureUrl}`);
  
  const meshRef = useRef();
  const groupRef = useRef();
  const [texture, setTexture] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  
  // Load texture
  useEffect(() => {
    const loader = new THREE.TextureLoader();
    loader.load(
      textureUrl,
      (loadedTexture) => {
        setTexture(loadedTexture);
        setIsLoaded(true);
        console.log(`[PhotoNode.jsx] Texture loaded for ${id}`);
      },
      undefined,
      (error) => {
        console.error(`[PhotoNode.jsx] Failed to load texture for ${id}:`, error);
        setIsLoaded(true); // Still mark as loaded to show fallback
      }
    );
  }, [textureUrl, id]);
  
  // Set position
  useEffect(() => {
    if (groupRef.current && position) {
      groupRef.current.position.set(...position);
    }
  }, [position]);
  
  const isActive = targetImage === id;
  const nodeOpacity = !targetImage || isActive ? 1 : 0.2;
  
  // Simple opacity animation
  useFrame((state, delta) => {
    if (meshRef.current && meshRef.current.material && isLoaded) {
      const currentOpacity = meshRef.current.material.opacity;
      const targetOpacity = nodeOpacity;
      meshRef.current.material.opacity = THREE.MathUtils.lerp(
        currentOpacity,
        targetOpacity,
        delta * 3
      );
    }
  });
  
  if (!isLoaded) {
    return null; // Don't render until loaded
  }

  return (
    <group
      ref={groupRef}
      onClick={(e) => {
        e.stopPropagation();
        setTargetImage(id);
        console.log(`[PhotoNode.jsx] Clicked ${id}`);
      }}
    >
      <Billboard>
        <mesh ref={meshRef} scale={[thumbWidth, thumbHeight, 1]}>
          <planeGeometry />
          <meshBasicMaterial
            map={texture}
            color={texture ? "white" : "red"}
            transparent={true}
            opacity={nodeOpacity}
            side={THREE.DoubleSide}
          />
        </mesh>
      </Billboard>
    </group>
  );
}
