import React from "react";
import {Canvas, useFrame, useThree} from '@react-three/fiber'
import {TrackballControls} from '@react-three/drei'
import {useRef, useState, useEffect} from 'react'
import useStore from '../store.js'
import { PhotoNode } from './PhotoNode.jsx'
import {setTargetImage} from '../actions.js'

function SceneContent() {
  console.log("[PhotoViz.jsx] SceneContent rendering");
  const {
    images,
    nodePositions,
    layout,
    targetImage,
    resetCam
  } = useStore(state => ({
    images: state.images,
    nodePositions: state.nodePositions,
    layout: state.layout,
    targetImage: state.targetImage,
    resetCam: state.resetCam
  }));
  
  const {camera} = useThree()
  const groupRef = useRef()
  const controlsRef = useRef()
  const [isAutoRotating, setIsAutoRotating] = useState(false)
  const rotationVelocityRef = useRef(0)

  const targetSpeed = 0.1 
  const acceleration = 0.5 

  const handleInteractionStart = () => {
    setIsAutoRotating(false)
    rotationVelocityRef.current = 0
  }

  const handleInteractionEnd = () => {
    // Could restart auto-rotation here if desired
  }

  // Simple camera positioning without complex animations
  useEffect(() => {
    if (camera && controlsRef.current) {
      if (targetImage) {
        // Focus on target image (simplified)
        setIsAutoRotating(false)
        rotationVelocityRef.current = 0
      } else {
        // Default view
        camera.position.set(0, 0, 195)
        controlsRef.current.target.set(0, 0, 0)
      }
    }
  }, [targetImage, camera])

  // Layout changes
  useEffect(() => {
    if (groupRef.current) {
      // Simple position changes without animation
      if (layout === 'grid') {
        groupRef.current.position.z = 150
      } else {
        groupRef.current.position.z = 0
      }
      
      if (resetCam) {
        groupRef.current.rotation.set(0, 0, 0)
        useStore.setState(state => {
          state.resetCam = false
        })
      }
    }
  }, [layout, resetCam])

  useFrame((_, delta) => {
    // Update camera Z position in store
    useStore.setState({ cameraCurrentZ: camera.position.z });

    // Simple auto-rotation
    let currentVelocity = rotationVelocityRef.current

    if (isAutoRotating) {
      currentVelocity += (targetSpeed - currentVelocity) * acceleration * delta
    } else {
      currentVelocity += (0 - currentVelocity) * acceleration * delta
    }

    rotationVelocityRef.current = currentVelocity

    if (
      groupRef.current &&
      Math.abs(currentVelocity) > 0.0001 &&
      layout !== 'grid' &&
      !targetImage
    ) {
      groupRef.current.rotation.y += currentVelocity * delta
    }

    if (controlsRef.current) {
      controlsRef.current.update()
    }
  })

  console.log("[PhotoViz.jsx] SceneContent returning JSX");
  return (
    <>
      <ambientLight intensity={2.3} />
      <TrackballControls
        ref={controlsRef}
        onStart={handleInteractionStart}
        onEnd={handleInteractionEnd}
        minDistance={20}
        maxDistance={195}
        noPan
      />
      <group ref={groupRef}>
        {images?.map(image => {
          const normPosArray = nodePositions?.[image.id];
          if (!normPosArray) {
            console.warn(`[PhotoViz.jsx] No position found for image ID: ${image.id}`);
            return null;
          }

          const scaledPosition = [
            (normPosArray[0] - 0.5) * 600,
            (normPosArray[1] - 0.5) * 600,
            ((normPosArray[2] || 0) - 0.5) * 600
          ];

          return (
            <PhotoNode
              key={image.id}
              id={image.id}
              position={scaledPosition}
              targetImage={targetImage}
            />
          )
        })}
      </group>
    </>
  )
}

export default function PhotoViz() {
  console.log("[PhotoViz.jsx] PhotoViz rendering");
  return (
    <Canvas
      camera={{position: [0, 0, 195], near: 0.1, far: 10000}}
      onPointerMissed={() => setTargetImage(null)}
    >
      <SceneContent />
    </Canvas>
  )
}
